.collection-item{
    width: 22%;
    display: flex;
    flex-direction: column;
    height: 330px;
    align-items: center;


    .image {
        width: 100%;
        height: 95%;
        background-size: cover;
        background-position: center;
        margin-bottom: 5px;
    }

    .collection-footer {
        width: 100%;
        height: 5%;
        display: flex;
        font-size: 18px;
        justify-content: space-between;
    

        .name {
            width: 90%;
            margin-bottom: 15px;
        }
    }
}